import { auth } from '../app';
import { signInWithPopup, GoogleAuthProvider, GithubAuthProvider } from 'firebase/auth';

const googleProvider = new GoogleAuthProvider();
const githubProvider = new GithubAuthProvider();

export default function handleUnauthenticatedUser() {
  const loginForm = document.getElementById('loginForm');
  if (loginForm) loginForm.classList.remove('hidden');

  setupSocialLogins();
}

function setupSocialLogins() {
  const googleLogin = document.getElementById('googleLogin');
  const githubLogin = document.getElementById('githubLogin');

  if (googleLogin) {
    googleLogin.addEventListener('click', () => signInWithPopup(auth, googleProvider));
  }

  if (githubLogin) {
    githubLogin.addEventListener('click', () => signInWithPopup(auth, githubProvider));
  }
}
